import React from 'react'
import "./css/Homescreen.css"
import Nav from './Nav'
import Banner from './Banner'
import Row from './Row'
import userrequest from './request'


console.log(userrequest.fetchNetflixOriginals);
const Homescreen = () => {
  return (
    <div className="Homescreen">
      <Nav />
      <Banner />
      <Row
        title="Netflix originals"
        fetchUrl={userrequest.fetchNetflixOriginals}
        isLargeRow={true}
      ></Row>
      <Row title="Trending Now" fetchUrl={userrequest.fetchTreding} />
      <Row title="Top Rated" fetchUrl={userrequest.fetchTopRated} />
      <Row title="Action Movies" fetchUrl={userrequest.fetchActionMovies} />
      <Row title="Horror Movies" fetchUrl={userrequest.fetchHorroMovies} />
      <Row title="Romance Movies" fetchUrl={userrequest.fetchRomanticMovies} />
      <Row
        title="Documentaries"
        fetchUrl={userrequest.fetchDocumentaries}
      />
      
    </div>
  );
}

export default Homescreen